<header className="fixed inset-x-0 top-0 z-50 border-b border-border/30 bg-background/80 backdrop-blur-md">
  <nav className="mx-auto flex max-w-6xl items-center justify-between px-6 py-3">
    {/* Logo */}
    <Link href="/" className="flex items-center gap-2">
      <Moon className="h-6 w-6 text-primary" />
      <span className="font-serif text-xl font-bold tracking-wide text-primary">
        밤의 비밀 소리 타로
      </span>
    </Link>

    {/* Desktop Navigation */}
    <div className="hidden md:flex flex-1 justify-center items-center gap-6">
      <Link href="#services" className="text-sm text-foreground/70 hover:text-primary transition">
        서비스
      </Link>
      <Link href="#about" className="text-sm text-foreground/70 hover:text-primary transition">
        소개
      </Link>
      <Link href="#testimonials" className="text-sm text-foreground/70 hover:text-primary transition">
        후기
      </Link>
      <Link
        href="/reservation"
        className="flex items-center gap-2 rounded-lg bg-primary px-4 py-2 text-sm font-medium text-primary-foreground hover:bg-primary/90 transition"
      >
        <Sparkles className="h-4 w-4" />
        예약하기
      </Link>
    </div>

    {/* Desktop Admin */}
    <div className="hidden md:flex ml-4">
      <Link
        href="/admin/login"
        className="text-xs text-foreground/60 border border-transparent px-3 py-1 rounded-full hover:border-primary/40 hover:bg-primary/5 hover:text-primary transition"
      >
        관리자
      </Link>
    </div>

    {/* Mobile Menu Button */}
    <button
      type="button"
      className="md:hidden text-foreground ml-2"
      onClick={() => setIsOpen(!isOpen)}
      aria-label={isOpen ? "메뉴 닫기" : "메뉴 열기"}
    >
      {isOpen ? <X className="h-6 w-6" /> : <Menu className="h-6 w-6" />}
    </button>
  </nav>

  {/* Mobile Menu */}
  {isOpen && (
    <div className="md:hidden border-t border-border/30 bg-background/95 backdrop-blur-md">
      <div className="flex flex-col gap-3 px-6 py-4">
        <Link href="#services" className="text-sm text-foreground/70 hover:text-primary" onClick={() => setIsOpen(false)}>서비스</Link>
        <Link href="#about" className="text-sm text-foreground/70 hover:text-primary" onClick={() => setIsOpen(false)}>소개</Link>
        <Link href="#testimonials" className="text-sm text-foreground/70 hover:text-primary" onClick={() => setIsOpen(false)}>후기</Link>
        <Link
          href="/reservation"
          className="flex items-center justify-center gap-2 rounded-lg bg-primary px-4 py-2 text-sm font-medium text-primary-foreground"
          onClick={() => setIsOpen(false)}
        >
          <Sparkles className="h-4 w-4" />
          예약하기
        </Link>
        <Link href="/admin/login" className="text-xs text-muted-foreground/70 hover:text-primary hover:underline" onClick={() => setIsOpen(false)}>관리자</Link>
      </div>
    </div>
  )}
</header>
